import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {path: '', loadChildren: () => import('./pages/inicio/inicio.module').then(Minicio => Minicio.InicioModule)},
  {path: 'registro', loadChildren: () => import('./pages/gestion-mantenimientos/gestion-mantenimientos.module').then(Mregistro => Mregistro.GestionMantenimientosModule)},
  {path: 'registro/:serie/:typemc', loadChildren: () => import('./pages/gestion-mantenimientos/gestion-mantenimientos.module').then(Mregistro => Mregistro.GestionMantenimientosModule)},
  {path: 'historial', loadChildren: () => import('./pages/historial/historial.module').then(Mhistorial => Mhistorial.HistorialModule)},
  {path: 'pendientes', loadChildren: () => import('./pages/pendientes/pendientes.module').then(Mpendientes => Mpendientes.PendientesModule)}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
